import React from 'react';

import styles from './UserCard.module.scss';

export default function UserCard({ user, expandCard }) {
  const getUserFullName = () => [user.name.first, user.name.last].join(' ');
  const getUserLocation = () => [user.location.state, user.location.city].join(', ');

  return (
    <div className={styles.userCard} onClick={() => expandCard(user.email, user.expanded)}>
      <div className={styles.visible}>
        <img className={styles.userImage} src={user.picture.medium} alt={getUserFullName()} />
        <div className={styles.userInfoWrapper}>
          <div className={styles.userName}>
            {getUserFullName()}
            <span className={styles.userBirthdate}>({new Date(user.dob.date).getFullYear()})</span>
          </div>
          <div className={styles.userLocation}>{getUserLocation()}</div>
        </div>
        <i className={`${styles.arrow} ${user.expanded ? styles.arrowUp : styles.arrowDown}`} />
      </div>
      <div className={`${styles.expandableContainer} ${user.expanded ? styles.expanded : ''}`}>
        <div className={styles.expandableBox}>
          <span className={styles.expandableTitle}>phone</span>
          <span className={styles.expandableDescription}>{user.phone}</span>
        </div>
        <div className={styles.expandableBox}>
          <span className={styles.expandableTitle}>email</span>
          <span className={styles.expandableDescription}>{user.email}</span>
        </div>
      </div>
    </div>
  );
}
