import React, { useState, useEffect } from 'react';

import UserCard from '../user-card';

import styles from './UserCards.module.scss';

import * as CONSTANTS from '../../constants';

export default function UserCards(props) {
  const [users, setUsers] = useState([]);

  const expandCard = (email, expanded) => {
    const usersUpdated = [...users];
    usersUpdated.map((user) => {
      if (user.email === email) {
        user.expanded = !expanded;
      } else {
        user.expanded = false;
      }
      return user;
    });

    setUsers(usersUpdated);
  };

  const fetchUsers = async () => {
    const response = await fetch(`${CONSTANTS.API_URL}?results=${CONSTANTS.API_LIMIT}`, {
      headers: {
        'Content-Type': 'application/json',
      },
    });
    const { results } = await response.json();
    setUsers(users.concat(results));
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  return (
    <div>
      {users.map((user) => (
        <UserCard key={user.email} user={user} expandCard={expandCard} />
      ))}
      <div className={styles.actionsContainer}>
        <button
          className={styles.loadMoreUsers}
          onClick={() => {
            fetchUsers();
          }}
        >
          Load More Users
        </button>
      </div>
    </div>
  );
}
